.wheel-menu {
    position: absolute;
    width: 80vw;
    height: 80vw;
    left: -40%;
    top: 50%;
    margin-top: -40vw;
    background-color: rgb(142, 142, 142);
    border-radius: 50%;
    border: 1vw solid white;
    outline: 5vw solid white;
    outline-offset: -5vw;
}

.main-title {
    display: flex;
    position: absolute;
    font-size: 4vw;
    width: 20vw;
    height: 20vw;
    left: 50%;
    top: 50%;
    margin-left: -10vw;
    margin-top: -10vw;
    color: black;
    text-align: center;
    align-items: center;
    justify-content: center;
}

.wheel-slot {
    display: flex;
    position: absolute;
    margin-left: -3vw;
    margin-top: -3vw;
    left: 50%;
    top: 50%;
    width: 6vw;
    height: 6vw;
    text-align: center;
    align-items: center;
    justify-content: center;
    font-family: 'consolas', 'Nanum Gothic';
}

.wheel-slot-label {
    display: flex;
    position: absolute;
    width: 6vw;
    height: 6vw;
    font-size: 5vw;
    text-align: center;
    align-items: center;
    justify-content: center;
}

.wheel-slot-title {
    display: flex;
    position: absolute;
    width: 40vw;
    height: 3vw;
    left: 13vw;
    font-size: 2vw;
    text-align: center;
    align-items: center;
    justify-content: left;
    color: darkgray;
}

.selected-label {
    font-size: 10vw;
}

.selected-title {
    font-size: 4vw;
    color: black;
}

.transitions-wrapper {
    position: relative;
    overflow: hidden;
    height: calc(var(--vh, 1vh) * 100);
    width: 100vw;
}

.right-enter {
    transform: translateX(100%);
}

.right-enter-active {
    transform: translateX(0);
    transition: transform 300ms ease-in-out;
}

.right-exit {
    transform: translateX(0);
}

.right-exit-active {
    transform: translateX(-100%);
    transition: transform 300ms ease-in-out;
}